<template>
  <div class="main-wrap">
    <div class="container">
      <span class="label">PX to VW</span>
      <form>
        <div class="input-container">
          <input class="input" type="text" v-model="pxToVw.viewportWidthPXVW">
          <label class="label">viewport width</label>
        </div>
        <div class="input-container">
          <input class="input" type="text" v-model="pxToVw.px">
          <label class="label">px</label>
        </div>
        <div class="input-container">
          <div class="input result" type="text">{{$store.state.pxToVwResult}}</div>
          <span class="label">result</span>
        </div>
        <small v-if="smallTextPXVW">Invalid input!</small>
        <a class="converse-btn-wrap" href="#">
          <div class="converse-btn" @click="pxToVwHub">CONVERSE</div>
        </a>
        <a class="converse-btn-wrap" href="#">
          <div class="converse-btn reset-btn" @click="resetPXVW">RESET</div>
        </a>
      </form>
    </div>
    <div class="container">
      <span class="label">VW to PX</span>
      <form>
        <div class="input-container">
          <input class="input" type="text" v-model="vwToPx.viewportWidthVWPX">
          <label class="label">viewport width</label>
        </div>
        <div class="input-container">
          <input class="input" type="text" v-model="vwToPx.vw">
          <label class="label">vw</label>
        </div>
        <div class="input-container">
          <div class="input result" type="text">{{$store.state.vwToPxResult}}</div>
          <span class="label">result</span>
        </div>
        <small v-if="smallTextVWPX">Invalid input!</small>
        <a class="converse-btn-wrap" href="#">
          <div class="converse-btn" @click="vwToPxHub">CONVERSE</div>
        </a>
        <a class="converse-btn-wrap" href="#">
          <div class="converse-btn reset-btn" @click="resetVWPX">RESET</div>
        </a>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Converter',
  data: function() {
    return {
      pxToVw: {viewportWidthPXVW: '', px: ''},
      vwToPx: {viewportWidthVWPX: '', vw: ''},
      smallTextPXVW: false,
      smallTextVWPX: false
    }
  },
  methods: {
    pxToVwHub() {
      if (this.pxToVw.viewportWidthPXVW / 0 != Infinity || this.pxToVw.px / 0 != Infinity) {
        this.smallTextPXVW = true
      } else {
        this.smallTextPXVW = false
        this.$store.commit('pxToVwConverter', this.pxToVw)
      }
    },
    vwToPxHub() {
      if (this.vwToPx.viewportWidthVWPX / 0 != Infinity || this.vwToPx.vw / 0 != Infinity) {
        this.smallTextVWPX = true
      } else {
        this.smallTextVWPX = false
        this.$store.commit('vwToPxConverter', this.vwToPx)
      }
    },
    resetPXVW() {
      this.pxToVw.viewportWidthPXVW = '';
      this.pxToVw.px = '';
      this.smallTextPXVW = false
      this.$store.commit('resetPXVW')
    },
    resetVWPX() {
      this.vwToPx.viewportWidthVWPX = '';
      this.vwToPx.vw = '';
      this.smallTextVWPX = false
      this.$store.commit('resetVWPX')
    }
  }
}
</script>

