import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

//Usage of Vuex in the project for educational purposes only

export default new Vuex.Store({
  state: {
    pxToVwResult: 0,
    vwToPxResult: 0
  },
  mutations: {
    pxToVwConverter(state, payload) {
      state.pxToVwResult = ((payload.px * 100) / payload.viewportWidthPXVW).toFixed(3)
    },
    vwToPxConverter(state, payload) {
      state.vwToPxResult = Math.round((payload.viewportWidthVWPX * payload.vw) / 100)
    },
    resetPXVW(state) {
      state.pxToVwResult = 0
    },
    resetVWPX(state) {
      state.vwToPxResult = 0
    }
  },
  actions: {
  },
  modules: {
  }
})
